"use client"

import { BookOpen, Users, ClipboardCheck, TrendingUp, Calendar, Clock, AlertCircle, Info, Plus } from "lucide-react"
import { StatCard } from "@/components/stat-card"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { useState, useEffect } from "react"

export default function DashboardPage() {
  const [isMobile, setIsMobile] = useState(false)
  const [isTablet, setIsTablet] = useState(false)

  useEffect(() => {
    const checkScreenSize = () => {
      setIsMobile(window.innerWidth < 768)
      setIsTablet(window.innerWidth >= 768 && window.innerWidth < 1024)
    }
    
    checkScreenSize()
    window.addEventListener('resize', checkScreenSize)
    return () => window.removeEventListener('resize', checkScreenSize)
  }, [])

  return (
    <div className="space-y-6 p-3 md:p-6">
      {/* Header */}
      <div className="space-y-2">
        <h2 className="text-2xl sm:text-3xl font-bold tracking-tight text-foreground">Dashboard</h2>
        <p className="text-sm sm:text-base text-muted-foreground">Welcome to the i-eSchool Admin Panel</p>
      </div>

      {/* Developer Notes - Mobile Optimized */}
      <Alert className="border-primary/50 bg-primary/5">
        <Info className="h-4 w-4 text-primary flex-shrink-0" />
        <AlertDescription className="text-xs sm:text-sm">
          <div>
            <strong className="font-semibold text-foreground">Developer Notes:</strong>
            <span className="text-muted-foreground">
              {" "}
              This frontend is for an interview exercise. Complete <strong>1 of 3 tasks:</strong>
            </span>
            <ul className="mt-2 ml-4 list-disc space-y-1 text-muted-foreground">
              <li>Replace mock data with a real database</li>
              <li>Implement Microsoft Teams webhook endpoint</li>
              <li>Build real APIs for classes, students, and attendance</li>
            </ul>
          </div>
        </AlertDescription>
      </Alert>

      {/* Stats Grid - Responsive */}
      <div className="grid gap-4 sm:gap-6 grid-cols-2 lg:grid-cols-4">
        <StatCard 
          title="Total Students" 
          value={145} 
          icon={Users} 
          description="Active students"
          compact={isMobile}
        />
        <StatCard 
          title="Total Classes" 
          value={12} 
          icon={BookOpen} 
          description="Ongoing courses"
          compact={isMobile}
        />
        <StatCard 
          title="Today's Attendance" 
          value="87%" 
          icon={ClipboardCheck} 
          description="126 of 145 present"
          compact={isMobile}
        />
        <StatCard 
          title="Avg. Performance" 
          value="84%" 
          icon={TrendingUp} 
          description="This semester"
          compact={isMobile}
        />
      </div>

      {/* Quick Actions for Mobile */}
      {isMobile && (
        <div className="space-y-3">
          <div className="grid grid-cols-2 gap-2">
            <Button variant="outline" size="sm" className="gap-2 h-auto py-3">
              <Calendar className="h-4 w-4" />
              <span className="text-xs">Mark Attendance</span>
            </Button>
            <Button variant="outline" size="sm" className="gap-2 h-auto py-3">
              <Plus className="h-4 w-4" />
              <span className="text-xs">Add Student</span>
            </Button>
          </div>
          <Button className="w-full gap-2">
            <AlertCircle className="h-4 w-4" />
            Generate AI Summary
          </Button>
        </div>
      )}

      {/* Overview Grid - Responsive */}
      <div className="grid gap-6 lg:grid-cols-2">
        {/* Recent Activity Card */}
        <Card>
          <CardHeader className="px-4 sm:px-6">
            <div className="flex items-center justify-between">
              <div>
                <CardTitle className="text-lg sm:text-xl">Recent Activity</CardTitle>
                <CardDescription className="text-sm sm:text-base">Latest updates from the system</CardDescription>
              </div>
              {!isMobile && (
                <Badge variant="outline" className="gap-1">
                  <Clock className="h-3 w-3" />
                  24h
                </Badge>
              )}
            </div>
          </CardHeader>
          <CardContent className="px-4 sm:px-6">
            <div className="space-y-3">
              {[
                { 
                  action: "New student enrolled", 
                  detail: "Amina Hassan - Mathematics 101", 
                  time: "2 hours ago",
                  type: "success"
                },
                { 
                  action: "Attendance marked", 
                  detail: "English Literature - 30/30 present", 
                  time: "4 hours ago",
                  type: "info"
                },
                { 
                  action: "Class created", 
                  detail: "Advanced Physics 301", 
                  time: "1 day ago",
                  type: "warning"
                },
                { 
                  action: "Teacher assigned", 
                  detail: "Dr. Johnson to Chemistry 202", 
                  time: "1 day ago",
                  type: "default"
                },
              ].map((item, index) => (
                <div key={index} className="flex items-start gap-3 text-sm p-2 rounded-lg hover:bg-accent/50">
                  <div className={`mt-1 h-2 w-2 rounded-full flex-shrink-0 ${
                    item.type === 'success' ? 'bg-green-500' :
                    item.type === 'info' ? 'bg-blue-500' :
                    item.type === 'warning' ? 'bg-amber-500' : 'bg-muted-foreground'
                  }`} />
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between gap-2">
                      <p className="font-medium text-foreground truncate">{item.action}</p>
                      {isMobile && (
                        <span className="text-xs text-muted-foreground whitespace-nowrap">{item.time}</span>
                      )}
                    </div>
                    <p className="text-muted-foreground truncate">{item.detail}</p>
                    {!isMobile && (
                      <p className="mt-1 text-xs text-muted-foreground">{item.time}</p>
                    )}
                  </div>
                </div>
              ))}
            </div>
            {!isMobile && (
              <Button variant="ghost" size="sm" className="w-full mt-4">
                View All Activity
              </Button>
            )}
          </CardContent>
        </Card>

        {/* Quick Actions Card */}
        <Card>
          <CardHeader className="px-4 sm:px-6">
            <CardTitle className="text-lg sm:text-xl">Quick Actions</CardTitle>
            <CardDescription className="text-sm sm:text-base">Common tasks and shortcuts</CardDescription>
          </CardHeader>
          <CardContent className="px-4 sm:px-6">
            <div className="space-y-3">
              {/* Mobile-friendly action items */}
              {[
                {
                  title: "Mark Today's Attendance",
                  description: "Record student attendance for all classes",
                  icon: ClipboardCheck,
                  action: "/attendance",
                  variant: "primary" as const
                },
                {
                  title: "Generate AI Summary",
                  description: "Get AI-powered insights on attendance",
                  icon: TrendingUp,
                  action: "/ai-summary",
                  variant: "secondary" as const
                },
                {
                  title: "Send Teams Message",
                  description: "Notify staff via Microsoft Teams",
                  icon: Users,
                  action: "/teams-integration",
                  variant: "outline" as const
                },
                {
                  title: "Add New Class",
                  description: "Create a new course or class",
                  icon: BookOpen,
                  action: "/classes",
                  variant: "outline" as const
                }
              ].map((item, index) => (
                <div 
                  key={index}
                  className={`rounded-lg border p-3 cursor-pointer transition-all hover:scale-[1.02] active:scale-[0.98] ${
                    item.variant === 'primary' 
                      ? 'border-primary bg-primary/5 hover:bg-primary/10' 
                      : 'border-border hover:bg-accent/50'
                  }`}
                  onClick={() => window.location.href = item.action}
                >
                  <div className="flex items-start gap-3">
                    <div className={`h-10 w-10 rounded-lg flex items-center justify-center flex-shrink-0 ${
                      item.variant === 'primary' 
                        ? 'bg-primary/10 text-primary' 
                        : 'bg-muted text-muted-foreground'
                    }`}>
                      <item.icon className="h-5 w-5" />
                    </div>
                    <div className="flex-1 min-w-0">
                      <p className="font-medium text-foreground truncate">{item.title}</p>
                      <p className="text-sm text-muted-foreground truncate">{item.description}</p>
                    </div>
                    {!isMobile && (
                      <Button 
                        size="sm" 
                        variant="ghost" 
                        className="opacity-0 group-hover:opacity-100 transition-opacity"
                      >
                        Go
                      </Button>
                    )}
                  </div>
                </div>
              ))}
            </div>
            
            {/* Performance Stats for Desktop/Tablet */}
            {!isMobile && (
              <div className="mt-6 pt-6 border-t">
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <span className="text-sm font-medium">System Performance</span>
                    <span className="text-sm font-medium text-primary">85%</span>
                  </div>
                  <Progress value={85} className="h-2" />
                  <div className="grid grid-cols-3 gap-4 text-xs text-muted-foreground">
                    <div>
                      <div className="font-medium">API Response</div>
                      <div>42ms avg</div>
                    </div>
                    <div>
                      <div className="font-medium">Uptime</div>
                      <div>99.8%</div>
                    </div>
                    <div>
                      <div className="font-medium">Active Users</div>
                      <div>24</div>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Upcoming Classes for Tablet/Desktop */}
      {!isMobile && (
        <Card>
          <CardHeader className="px-4 sm:px-6">
            <CardTitle className="text-lg sm:text-xl">Upcoming Classes</CardTitle>
            <CardDescription className="text-sm sm:text-base">Next 4 hours schedule</CardDescription>
          </CardHeader>
          <CardContent className="px-4 sm:px-6">
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
              {[
                { time: "9:00 AM", subject: "Mathematics 101", teacher: "Dr. Smith", room: "Room 301" },
                { time: "10:30 AM", subject: "English Literature", teacher: "Ms. Johnson", room: "Room 204" },
                { time: "1:00 PM", subject: "Physics 201", teacher: "Prof. Williams", room: "Lab 101" },
                { time: "2:30 PM", subject: "Computer Science", teacher: "Dr. Brown", room: "Lab 203" },
              ].map((cls, index) => (
                <div key={index} className="border rounded-lg p-3 hover:bg-accent/50 transition-colors">
                  <div className="flex items-center justify-between mb-2">
                    <Badge variant="secondary" className="font-mono">
                      {cls.time}
                    </Badge>
                    <Badge variant="outline" className="text-xs">
                      {cls.room}
                    </Badge>
                  </div>
                  <h4 className="font-semibold text-sm truncate">{cls.subject}</h4>
                  <p className="text-xs text-muted-foreground truncate">Teacher: {cls.teacher}</p>
                  {isTablet && (
                    <Button size="sm" variant="ghost" className="w-full mt-2 text-xs">
                      View Details
                    </Button>
                  )}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Mobile Bottom Navigation */}
      {isMobile && (
        <div className="fixed bottom-0 left-0 right-0 bg-background border-t p-3 z-10">
          <div className="grid grid-cols-4 gap-2">
            <Button variant="ghost" size="sm" className="flex-col h-auto py-2">
              <BookOpen className="h-4 w-4 mb-1" />
              <span className="text-xs">Classes</span>
            </Button>
            <Button variant="ghost" size="sm" className="flex-col h-auto py-2">
              <Users className="h-4 w-4 mb-1" />
              <span className="text-xs">Students</span>
            </Button>
            <Button variant="ghost" size="sm" className="flex-col h-auto py-2">
              <ClipboardCheck className="h-4 w-4 mb-1" />
              <span className="text-xs">Attendance</span>
            </Button>
            <Button variant="ghost" size="sm" className="flex-col h-auto py-2">
              <TrendingUp className="h-4 w-4 mb-1" />
              <span className="text-xs">More</span>
            </Button>
          </div>
        </div>
      )}
    </div>
  )
}